import React, { useState, useRef, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, Typography, Row, Button, Form, Table, Space } from "antd";
import withAuth from "../../HOC/withAuth";
import {
  Card,
  FormInput,
  GridLarge,
  GridSmall,
  IconButton,
} from "../../Components";
import { FiDownload, FiPlusSquare, FiSearch, FiUpload } from "react-icons/fi";
import { brightColor, inActiveColor, textColor } from "../../Utils/colors";
import { useDisclosure, useGetDataPagination } from "../../Hooks";
import debounce from "lodash.debounce";
import ModalRegisterUser from "../../Components/UserManagement/ModalRegisterUser";

const { Title, Text } = Typography;

const UserList = () => {
  const [updateData, setUpdateData] = useState(0);
  const [page, setPage] = useState(1);
  const [perPage, setPerPage] = useState(20);
  const { data, pagination, loading } = useGetDataPagination(
    `users?page=${page}&perpage=${perPage}`,
    updateData
  );

  const [searchData, setSearchData] = useState("");
  const [dataTemp, setDataTemp] = useState({});
  const [updating, setUpdating] = useState(false);

  const newUser = useMemo(
    () =>
      data.length === 0
        ? []
        : data.filter((item) =>
            item.emp_name?.toLowerCase().includes(searchData?.toLowerCase())
          ),
    [data, searchData]
  );

  const { isOpen, onOpen, onClose } = useDisclosure();
  const navigate = useNavigate();

  const handleSearchData = useRef(
    debounce((valueSearch) => setSearchData(valueSearch), 1000)
  ).current;

  const handleRegister = () => {
    setUpdating(false);
    onOpen();
  };

  const handelUpdateUser = (record) => {
    setUpdating(true);
    setDataTemp(record);
    onOpen();
  };

  const columns = [
    {
      title: "#",
      dataIndex: "user_id",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => handelUpdateUser(record)}
        >
          {text}
        </Text>
      ),
      width: 70,
      fixed: "left",
    },
    {
      title: "Name",
      dataIndex: "emp_name",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => handelUpdateUser(record)}
        >
          {text}
        </Text>
      ),
      fixed: "left",
    },
    {
      title: "Email",
      dataIndex: "email",
      width: 200,
    },
    {
      title: "DOB",
      dataIndex: "emp_birth_date",
    },
    {
      title: "Division",
      dataIndex: "division_name",
    },
    {
      title: "Phone",
      dataIndex: "mobile_phone",
    },
    {
      title: "Gender",
      dataIndex: "gender",
    },
    {
      title: "Status",
      dataIndex: "status",
    },
    {
      title: "Address",
      dataIndex: "address",
      ellipsis: true,
    },
    {
      title: "Join Date",
      dataIndex: "join_date",
    },
    {
      title: "Resign Date",
      dataIndex: "resign_date",
    },
    {
      title: "Supervisor",
      dataIndex: "supervisor",
    },
    {
      title: "Role",
      dataIndex: "role_name",
    },
  ];

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>User Management</Breadcrumb.Item>
      </Breadcrumb>
      <ModalRegisterUser
        visible={isOpen}
        onCancel={onClose}
        setUpdateData={setUpdateData}
        state={dataTemp}
        updating={updating}
      />
      <div>
        <Card>
          <Title
            level={3}
            type="secondary"
            style={{ marginRight: 20, marginBottom: 30 }}
          >
            User List
          </Title>
          <Row gutter={[16, 10]}>
            <GridLarge>
              <Space wrap>
                <Button
                  shape="round"
                  icon={<FiPlusSquare style={{ marginRight: 6 }} />}
                  onClick={handleRegister}
                >
                  Register User
                </Button>
                <IconButton
                  icon={<FiUpload size={20} />}
                  toolTip="Upload data user"
                  onClick={() => navigate("/user-management/upload")}
                />
                <a href="/uploaduser.csv" target="_blank" rel="noreferrer">
                  <IconButton
                    icon={<FiDownload color={textColor} size={20} />}
                    style={{ backgroundColor: brightColor }}
                    toolTip="Download template user"
                  />
                </a>
              </Space>
            </GridLarge>
            <GridSmall>
              <Form name="searchUser" autoComplete="off">
                <FormInput
                  name="search"
                  prefix={<FiSearch color={inActiveColor} />}
                  allowClear={true}
                  placeholder="serach user"
                  value={searchData}
                  onChange={(val) => handleSearchData(val.target.value)}
                />
              </Form>
            </GridSmall>
          </Row>
          <div style={{ marginTop: "20px" }}>
            <Table
              columns={columns}
              dataSource={newUser}
              rowKey={(record) => record.user_id}
              loading={loading}
              scroll={{ y: 500, x: 1800 }}
              pagination={{
                total: pagination.total_data,
                showTotal: (total, range) =>
                  `${range[0]}-${range[1]} of ${total} items`,
                showQuickJumper: true,
                defaultCurrent: 1,
                onChange: (pageNumber) => setPage(pageNumber),
                showSizeChanger: true,
                onShowSizeChange: (current, pageSize) => setPerPage(pageSize),
                defaultPageSize: perPage,
                pageSizeOptions: [20, 60, 100, 140],
              }}
            />
          </div>
        </Card>
      </div>
    </div>
  );
};

export default withAuth(UserList, [1]);
