import React, { useState, useEffect, useMemo } from "react";
import { Modal, Form, Divider, Button, message, Select } from "antd";
import { FormInput, FlexEnd, FormSelect, FormPassword } from "..";
import { useGetData, usePostData, useUpdateData } from "../../Hooks";
import { setInput } from "../../Utils/myFormat";
import { FiNavigation } from "react-icons/fi";

const { Option } = Select;

const ModalRegisterUser = ({
  visible,
  onCancel,
  setUpdateData,
  state,
  updating,
}) => {
  const { data: dataEmployee, loading: loadingEmployee } =
    useGetData("employee");
  const { data: dataRole, loading: loadingRole } = useGetData("users/role");

  const [empId, setEmpId] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPass, setConfirmPass] = useState("");
  const [role, setRole] = useState("");
  const [isActive, setIsActive] = useState("");

  const resetState = () => {
    setEmpId("");
    setEmail("");
    setPassword("");
    setConfirmPass("");
    setRole("");
    setIsActive("");
    form.resetFields();
  };

  const [form] = Form.useForm();

  const newEmployee = useMemo(() => dataEmployee, [dataEmployee]);
  const newRole = useMemo(() => dataRole, [dataRole]);

  const [dataPost, setDataPost] = useState({
    url: "",
    data: {},
    trigger: 0,
  });
  const [dataUpdate, setDataUpdate] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess: successRegis, loading: loadingRegis } = usePostData(
    dataPost.url,
    dataPost.data,
    dataPost.trigger
  );
  const { onSuccess: successUpdate, loading: loadingUpdate } = useUpdateData(
    dataUpdate.url,
    dataUpdate.data,
    dataUpdate.trigger
  );

  const handleRegisterUser = () => {
    if (!empId || !email || !password || !role || isActive === "") {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    if (password.length < 8) {
      message.warn("Password panjang minimal 8 karakter");

      return null;
    }

    if (confirmPass.length < 8) {
      message.warn("Konfirmasi password panjang minimal 8 karakter");

      return null;
    }

    if (password !== confirmPass) {
      message.warn("Konfirmasi password tidak sesuai");

      return null;
    }

    const dataPost = {
      employe_id: empId,
      email: email,
      password: confirmPass,
      role_id: role,
      is_active: isActive,
    };
    console.log("data post: ", dataPost);

    setDataPost({
      url: "auth/signup",
      data: dataPost,
      trigger: new Date().getTime(),
    });
  };

  const handleUpdateUser = () => {
    if (
      !state.user_id ||
      !empId ||
      !email ||
      !password ||
      !role ||
      isActive === ""
    ) {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    if (password.length < 8) {
      message.warn("Password panjang minimal 8 karakter");

      return null;
    }

    if (confirmPass.length < 8) {
      message.warn("Konfirmasi password panjang minimal 8 karakter");

      return null;
    }

    if (password !== confirmPass) {
      message.warn("Konfirmasi password tidak sesuai");

      return null;
    }

    const dataUpdate = {
      user_id: state.user_id,
      email: email,
      password: confirmPass,
      active: isActive,
      role_id: role,
      employe_id: empId,
    };
    console.log("data update: ", dataUpdate);

    setDataUpdate({
      url: "users/edit",
      data: dataUpdate,
      trigger: new Date().getTime(),
    });
  };

  useEffect(() => {
    if (successRegis.includes("success")) {
      message.success(`Berhasil Register ${email}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }

    if (successUpdate.includes("success")) {
      message.success(`Berhasil Update User ${email}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [successRegis, successUpdate]);

  useEffect(() => {
    if (state.user_id && updating) {
      setEmpId("");
      setEmail(state.email);
      setRole(state.role_id);
      setIsActive("");
      form.setFieldsValue({
        empId: "",
        email: state.email,
        role: state.role_id,
        isActive: "",
      });
    } else {
      resetState();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [state, updating]);

  return (
    <Modal
      title={updating ? "Update User" : "Register User"}
      visible={visible}
      onCancel={onCancel}
      footer={false}
    >
      <Form form={form} name="createsite" autoComplete="off">
        <FormSelect
          name="empId"
          label="Employee"
          requiredLabel
          required
          message="Please select employee!"
          placeholder="select employee"
          allowClear
          showSearch
          optionFilterProp="children"
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
          onChange={(e) => setEmpId(e)}
          loading={loadingEmployee}
          disabled={loadingEmployee}
        >
          {newEmployee.map((item) => (
            <Option value={item.id} key={item.id}>
              {`[${item.id}] ${item.name}`}
            </Option>
          ))}
        </FormSelect>
        <FormInput
          label="Email"
          requiredLabel
          name="email"
          required
          message="please input email!"
          placeholder="input email"
          onChange={(e) => setInput(e, setEmail)}
        />
        <FormPassword
          label="Password"
          requiredLabel
          name="password"
          required
          message="Please input your password!"
          placeholder="enter your password"
          onChange={(e) => setInput(e, setPassword)}
        />
        <FormPassword
          label="Confirm Password"
          requiredLabel
          name="confirmPass"
          required
          message="Please input your confirm password!"
          placeholder="enter your confirm password"
          onChange={(e) => setInput(e, setConfirmPass)}
        />
        <FormSelect
          name="role"
          label="Role"
          requiredLabel
          required
          message="Please select role!"
          placeholder="select role"
          allowClear
          showSearch
          optionFilterProp="children"
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
          onChange={(e) => setRole(e)}
          loading={loadingRole}
          disabled={loadingRole}
        >
          {newRole.map((item) => (
            <Option value={item.id} key={item.id}>
              {`[${item.id}] ${item.name}`}
            </Option>
          ))}
        </FormSelect>
        <FormSelect
          name="isActive"
          label="Status"
          requiredLabel
          required
          message="Please select status!"
          placeholder="select status"
          onChange={(e) => setIsActive(e)}
        >
          <Option value={1}>Active</Option>
          <Option value={0}>Inactive</Option>
        </FormSelect>
        <Divider />
        <FlexEnd>
          <Form.Item>
            {updating ? (
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation style={{ marginRight: "6px" }} />}
                onClick={handleUpdateUser}
                loading={loadingUpdate}
                htmlType="submit"
              >
                Update User
              </Button>
            ) : (
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation style={{ marginRight: "6px" }} />}
                onClick={handleRegisterUser}
                loading={loadingRegis}
                htmlType="submit"
              >
                Register User
              </Button>
            )}
          </Form.Item>
        </FlexEnd>
      </Form>
    </Modal>
  );
};

export default ModalRegisterUser;
